// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import '@/styles/index.styl'
import './common/utils/highlight'
import 'highlight.js/styles/default.css'
import './common/utils/directive'
import App from './App'
import 'babel-polyfill'
import 'vant/lib/index.css'

import VueClipboards from "vue-clipboards"

Vue.use(VueClipboards)

import {Button, Notify, Form, Field, Popup, Area, Switch, Icon,
  Dialog, Swipe, SwipeItem,Tab,Tabs, RadioGroup, Radio, Search,
  Divider, GoodsAction, GoodsActionIcon, GoodsActionButton, ImagePreview, Sticky,
  Checkbox, CheckboxGroup, TreeSelect, Grid, GridItem, Image as VanImage, Col, Row,
  Uploader, Stepper,Picker, Toast, Step, Steps, Empty, ActionSheet, Overlay, DropdownMenu,
  DropdownItem, List, Loading, Sku , Cell, CellGroup, Badge} from 'vant';

Vue.use(Button).use(Notify).use(Form).use(Field).use(Popup).use(Area).use(Switch).use(Icon)
.use(Dialog).use(Swipe).use(SwipeItem).use(Tab).use(Tabs).use(RadioGroup).use(Radio).use(Search)
.use(Divider).use(GoodsAction).use(GoodsActionButton).use(GoodsActionIcon).use(ImagePreview).use(Sticky)
.use(Checkbox).use(CheckboxGroup).use(TreeSelect).use(Grid).use(GridItem).use(VanImage).use(Row).use(Col)
.use(Stepper).use(Uploader).use(Picker).use(Toast).use(Step).use(Steps).use(ActionSheet).use(Empty).use(Overlay).use(DropdownMenu).use(DropdownItem)
.use(List).use(Loading).use(Sku).use(CellGroup).use(Cell).use(Badge)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.directive('ban-save', {
  inserted(elem, vnode) {
    window.addEventListener('keydown', function (e) {
        if(e.keyCode == 83 && (navigator.platform.match('Mac') ? e.metaKey : e.ctrlKey)){
            e.preventDefault();
        }
    })
    document.oncontextmenu = function(){
        return false;
    }
    document.querySelector('iframe') !== null ? document.querySelector('iframe').onload = function () {
      // iframe 禁用 右键
      this.contentWindow.document.oncontextmenu = function(){
        return false;

      }
      // iframe 禁用 ctrl + s
      this.contentWindow.addEventListener('keydown', function (e) {
          if(e.keyCode == 83 && (navigator.platform.match('Mac') ? e.metaKey : e.ctrlKey)){
              e.preventDefault();
              e.stopPropagation();
          }
      }, true)
    } : ''
  }
})




